import clsx from 'clsx';
import React, { useRef, forwardRef, useImperativeHandle } from 'react';
import { addProps, useIsomorphicLayoutEffect } from '@element/react-common';
import { createPopper } from '@popperjs/core';
import { IconPropTypes, IconDefaultProps } from './generated/propTypes';

const Icon = forwardRef((props, externalRef) => {
    const {
        ariaHidden,
        ariaLabel,
        ariaLabelledBy,
        badge,
        badgePlacement,
        className,
        icon,
        iconType,
        pointer,
        size,
        iconSize,
        variant,
        ...customProps
    } = props;

    const iconRef = useRef();
    const badgeRef = useRef();
    const popperInstance = useRef();

    useImperativeHandle(externalRef, () => iconRef.current, []);

    useIsomorphicLayoutEffect(() => {
        if (badge) {
            let modifiers;
            if (badge.props.counter != null) {
                modifiers = [
                    {
                        name: 'flip',
                        options: {
                            fallbackPlacements: ['right', 'left']
                        }
                    },
                    {
                        name: 'offset',
                        options: {
                            offset: ({ placement }) => {
                                if (placement === 'right-start') {
                                    return [-5, -10];
                                }
                                return [-7, -10];
                            }
                        }
                    }
                ];
            } else {
                modifiers = [
                    {
                        name: 'flip',
                        options: {
                            fallbackPlacements: ['right-start', 'left-start']
                        }
                    },
                    {
                        name: 'offset',
                        options: {
                            offset: [0, -10]
                        }
                    }
                ];
            }

            popperInstance.current = createPopper(
                iconRef.current,
                badgeRef.current,
                {
                    placement: badgePlacement,
                    modifiers
                }
            );
        }
        return () => {
            popperInstance?.current?.destroy();
        };
    }, [badge, badgePlacement]);

    const classes = clsx(
        'lmnt',
        'lmnt-icon',
        iconType === 'filled' && 'material-icons',
        iconType === 'outlined' && 'material-icons-outlined',
        iconType === 'rounded' && 'material-icons-round',
        iconType === 'two-tone' && 'material-icons-two-tone',
        iconType === 'sharp' && 'material-icons-sharp',
        pointer && 'lmnt-icon-pointer',
        size === 'medium' || (iconSize === 'medium' && 'lmnt-icon--medium'),
        size === 'large' || (iconSize === 'large' && 'lmnt-icon--large'),
        size === 'xlarge' || (iconSize === 'xlarge' && 'lmnt-icon--xlarge'),
        (variant === 'primary' ||
            variant === 'primary-alt' ||
            variant === 'muted' ||
            variant === 'danger' ||
            variant === 'danger-alt' ||
            variant === 'filled-primary' ||
            variant === 'filled-secondary' ||
            variant === 'filled-muted' ||
            variant === 'filled-danger' ||
            variant === 'filled-danger-alt' ||
            variant === 'blue' ||
            variant === 'gray' ||
            variant === 'green' ||
            variant === 'orange' ||
            variant === 'purple' ||
            variant === 'red' ||
            variant === 'teal' ||
            variant === 'yellow') &&
            'lmnt-icon--fill',
        (variant === 'danger-alt' || variant === 'filled-danger-alt') &&
            'lmnt-icon--fill-danger-alt',
        (variant === 'danger' || variant === 'filled-danger') &&
            'lmnt-icon--fill-danger',
        (variant === 'primary' || variant === 'filled-primary') &&
            'lmnt-icon--fill-primary',
        (variant === 'primary-alt' || variant === 'filled-secondary') &&
            'lmnt-icon--fill-secondary',
        (variant === 'muted' || variant === 'filled-muted') &&
            'lmnt-icon--fill-muted',
        (variant === 'secondary-alt' || variant === 'color-on-dark') &&
            'lmnt-icon--color-on-dark',
        variant === 'color-primary' && 'lmnt-icon--color-primary',
        variant === 'color-secondary' && 'lmnt-icon--color-secondary',
        variant === 'blue' && 'lmnt-icon--utility-blue',
        variant === 'gray' && 'lmnt-icon--utility-gray',
        variant === 'green' && 'lmnt-icon--utility-green',
        variant === 'orange' && 'lmnt-icon--utility-orange',
        variant === 'purple' && 'lmnt-icon--utility-purple',
        variant === 'red' && 'lmnt-icon--utility-red',
        variant === 'teal' && 'lmnt-icon--utility-teal',
        variant === 'yellow' && 'lmnt-icon--utility-yellow',
        !badge &&
            (size === 'xsmall' ||
                (iconSize === 'xsmall' && 'lmnt-icon--xsmall')),
        !badge &&
            (size === 'small' || (iconSize === 'small' && 'lmnt-icon--small')),
        className
    );

    return (
        <i
            className={classes}
            aria-label={ariaLabel}
            aria-labelledby={ariaLabelledBy}
            aria-hidden={ariaLabel || ariaLabelledBy ? ariaHidden : false}
            {...customProps}
            ref={iconRef}
        >
            {icon}
            {addProps(badge, {
                ref: badgeRef
            })}
        </i>
    );
});

Icon.displayName = 'Icon';
Icon.propTypes = IconPropTypes;
Icon.defaultProps = IconDefaultProps;

export default Icon;
